@function vw($px) {
  @return $px/375*100vw;
}
@mixin abs($top,$left){
  position: absolute;
  top: vw($top);
  left: vw($left)
}

.barrage-wrap{
  @include abs(100, 0);
  width: 100%;
  height: vw(100);
  overflow: hidden;
  // border: 1px solid red;
}

.bullet{
  position: absolute;
  top:0;
  left: 100vw;
  width: auto;
  white-space: nowrap;
  padding: vw(8) vw(16);
  margin-left: 5vw;
  text-align: center;
  background:rgba(255,255,255,0.8);
  border:2px solid rgba(255, 255, 255, 1);
  border-radius:22px;
  font-size: vw(20);
  font-family:PingFang-SC-Medium;
  font-weight:500;
  color: #111;
  animation: move1 7s linear infinite;
}

@keyframes move1 {
  0% {
    transform: translate(0, 0) scale(0.5);
  }
  100% {
    transform: translate(-150vw, 0) scale(0.5);
  }
}



