@function vw($px) {
  @return $px/375*100vw;
}
@mixin abs($top,$left){
  position: absolute;
  top: vw($top);
  left: vw($left)
}

.preloader-fish-eat {
  background-image: url("https://s2.ax1x.com/2019/08/10/eO1lYq.png");
  background-image: url("https://s2.ax1x.com/2019/08/10/eO1fAA.png");
  background-image: url("https://s2.ax1x.com/2019/08/10/eO1lYq.png");
  background-image: url("https://s2.ax1x.com/2019/08/10/eO3S3V.png");
  background-image: url("https://s2.ax1x.com/2019/08/05/eRc1HA.png");
  display: inline;
}

.fishes-wrap{
  position: absolute;
  top: 32%;
  left: 0;
  width: 100%;
  height: vw(300);
}

.bg-bubble{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: url('https://s2.ax1x.com/2019/08/08/e7gQEV.png');
  animation: bubble 2200ms infinite steps(36);
}

.turtle{
  position: absolute;
  top: 35vw;
  left: 1vw;
  width: vw(70);
  height: vw(50);
  background: url('https://s2.ax1x.com/2019/08/10/eOJV4s.png');
  animation: turtle 1000ms infinite steps(10);
  transform: scale(0.6);
}

.fish1{
  position: absolute;
  top: 10vw;
  left: vw(-100);
  width: 145px;
  height: 70px;
  background: url('https://s2.ax1x.com/2019/08/10/eOGz9I.png');
  animation: fish1 1000ms infinite steps(10), swim1 32s linear infinite;
}

.fish2{
  position: absolute;
  top: 15vw;
  left: vw(-60);
  width: 75px;
  height: 70px;
  background: url('https://s2.ax1x.com/2019/08/10/eOJpgP.png');
  animation: fish2 1800ms infinite steps(10), swim2 43s linear infinite;
}

.fish3{
  position: absolute;
  top: 35vw;
  left: vw(-80);
  width: 90px;
  height: 80px;
  transition: all 0.5s;
  background: url('https://s2.ax1x.com/2019/08/10/eOJPu8.png');
  animation: fish3 3000ms infinite steps(10), swim3 20s linear infinite;
}

.fish4{
  position: absolute;
  top: 0;
  left: 101vw;
  width: 85px;
  height: 80px;
  background: url('https://s2.ax1x.com/2019/08/10/eOJiDS.png');
  background-repeat: repeat-x;
  animation: fish4 1200ms infinite steps(10), swim4 15s linear infinite;
}

.fish5{
  position: absolute;
  top: 45vw;
  left: 100vw;
  width: 75px;
  height: 70px;
  background: url('https://s2.ax1x.com/2019/08/10/eOJAEQ.png');
  animation: fish5 2200ms infinite steps(10), swim5 50s linear infinite;
}

.paused{
  animation-play-state: paused;
  opacity: 0;
}

.fish-eat1{
  position: absolute;
  top: -3vw;
  left: vw(20);
  width: 180px;
  height: 200px;
  background: url('https://s2.ax1x.com/2019/08/10/eO1lYq.png');
  animation: fishEat1 3000ms infinite steps(168), swimEat1 8s linear infinite;
}

.fish-eat2{
  position: absolute;
  top: 10vw;
  left: vw(50);
  width: 150px;
  height: 100px;
  background: url('https://s2.ax1x.com/2019/08/10/eO1fAA.png');
  animation: fishEat2 3200ms infinite steps(168), swimEat2 7s linear infinite;
}

.fish-eat3{
  position: absolute;
  top: 20vw;
  left: vw(30);
  width: 120px;
  height: 160px;
  background: url('https://s2.ax1x.com/2019/08/10/eO14ht.png');
  animation: fishEat3 3000ms infinite steps(168), swimEat3 6s linear infinite;
}

.fishEat4{
  position: absolute;
  top: -10vw;
  left: 60vw;
  width: 160px;
  height: 160px;
  background: url('https://s2.ax1x.com/2019/08/10/eO3S3V.png');
  animation: fishEat4 3000ms infinite steps(168), swimEat4 7s linear infinite;
}

.fish-eat5{
  position: absolute;
  top: 30vw;
