  height: 160px;
  background: url('https://s2.ax1x.com/2019/08/05/eRc1HA.png');
  animation: fishEat5 5000ms infinite steps(168), swimEat5 8s linear infinite;
}


@keyframes bubble {
  100% {
    background-position: 0 -100%;
  }
} 

@keyframes turtle {
  100% {
    background-position: -800px 0;
  }
} 

@keyframes fish1 {
