@keyframes swim1 {
  0% {
    transform: translate(0, 0) scale(0.6);
  }
  50% {
    transform: translate(vw(420), 0) scale(0.6);
  }
  51% {
    transform: translate(vw(420), 0) scale(-0.6,0.6);
  }
  100% {
    transform: translate(0, 0) scale(-0.6,0.6);
  }
}

