@function vw($px) {
  @return $px/375*100vw;
}
.bubble-wrap{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 80vw;
  z-index: 5;
}

.bubble{
  height: vw(42);
  width: vw(42);
  color: #fff;
  line-height: vw(42);
  font-weight: bold;
  font-size: vw(16);
  background: url(https://gw.alicdn.com/tfs/TB1p6p.cuP2gK0jSZFoXXauIVXa-168-168.png);
  background-size: cover;
  position: absolute;
  transition: all 2s;
  opacity: 1;

  &.fly-to-intimacy{
    top: vw(40) !important;
    left: vw(120) !important;
    transform: scale(.3);
    opacity: 0;
  }
}
