body,html {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
  width: 100%;
  overflow: hidden;
  position: relative;
}

@function vw($px) {
  @return $px/375*100vw;
}
@mixin abs($top,$left){
  position: absolute;
  top: vw($top);
  left: vw($left)
}

@mixin cover-adon(){
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}


.App-fish {
  height: 90vh;
  position: relative;
  z-index: 0;

  .shop-name{
    font-weight: bold;
    color: #fff;
    font-size: vw(14);
    position: absolute;
    top: vw(10);
    left: vw(11);
  }

  .info-wrap{
    position: absolute;
    width: vw(149);
    height: vw(50);
    left: vw(12);
    top: vw(32);
    border-radius: vw(25);
    overflow: hidden;
    &::before{
      @include cover-adon();
      background: #fff;
      opacity: .6;
      z-index: 0;
    }
    .avater{
      position: absolute;
      left: 0;
      top: 0;
      height: vw(50);
      width: vw(50);
      background: #fff;
      border-radius: 50%;
    }
    .medal{
      @include abs(6,61);
      height: vw(41);
      width: vw(33);
      background:url('https://b2.bmp.ovh/imgs/2019/08/c7117730a75b7714.png');
      background-size: cover;
      text-align: center;
      .medal-text{
        font-size: vw(11);
        transform: scale(0.7);
        display: inline-block;
        position: relative;
        line-height: vw(30);
        color: #6E4026;
      }
    }
    .intimacy-wrap{
      @include abs(7,100);
      height: vw(41);
      width: vw(33);
      .intimacy-icon{
        height: vw(25);
        width: vw(29);
      }
      .intimacy-count{
        font-size: vw(11);
        position: relative;
        color: #6E4026;
        line-height: 1.1;
      }
    }
  }

}
.rule{
  @include abs(6,304);
  height: vw(24);
  width: vw(57);
  z-index: 20;
  .rule-text{
    font-size: vw(12);
    transform: scale(.8);
    line-height: vw(24);
    color: #fff;
  }
  &::before{
    @include cover-adon();
    background: url(https://b2.bmp.ovh/imgs/2019/08/1a56f23b6cf4529d.png);
    background-size: cover;
  }
}

.dialog{
  background: url(https://gw.alicdn.com/tfs/TB1x7xybKbviK0jSZFNXXaApXXa-1136-1676.png);
  background-size: cover;
  height: vw(420);
  width: vw(284);
  position: relative;
  text-align: left;
  padding: vw(80) vw(17) 0 vw(17);
  box-sizing: border-box;
  color: #A16C40;
  font-size: vw(13);
  ul{
    padding: 0 vw(19);
  }
  li{
    font-weight: bold;
  }
}
.dialog-close{
  width: vw(35);
  height:vw(35);
  display: inline-block;
  background: url(https://gw.alicdn.com/tfs/TB1foL_coD1gK0jSZFGXXbd3FXa-140-140.png);
  background-size: cover;
  margin-top: 10px;
}

.bg-wrap,.grass-wrap{
  position: absolute;
  height: 90vh;
  width: 100%;
  top: 0;
}

.grass{
  position: absolute;
  z-index: 2;
  top: 0;
  height: 100%;
  width: 100%;
}


.actions{
  position: absolute;
  right: vw(8);
  bottom: vw(17);
  z-index: 10;
  >div{
    height: vw(64);
    width: vw(64);
    position: relative;
  }
  .count{
    height: vw(13);
    background: #FF4362;
    color: #fff;
    border-radius: vw(5);
    font-size: vw(12);
    line-height: vw(13);
    padding: 0 vw(2);
    position: absolute;
    right: vw(10);
    top: vw(7);
  }
  .feed{
    background: url(https://gw.alicdn.com/tfs/TB1qwMhcbj1gK0jSZFOXXc7GpXa-248-256.png);
    background-size: cover;
  }
  .mission{
    background: url(https://gw.alicdn.com/tfs/TB11EZfcbj1gK0jSZFuXXcrHpXa-248-262.png);
    background-size: cover;
  }
  .award{
    background: url(https://gw.alicdn.com/tfs/TB1tdweckY2gK0jSZFgXXc5OFXa-248-252.png);
    background-size: cover;
  }
  
}
.custom-dialog{
  z-index: 12;
  position: absolute;
  top: 30vh;
  width: 100%;
  .title{
    background: #fff;
    border-radius: 15px 15px 0 0;
    font-weight: bold;
    color: #333;
    height: vw(50);
    line-height: vw(50);
    padding-left: vw(14);
    &::before{
      content: '';
      position: absolute;
      height: vw(16);
      width: vw(16);
      background: url(https://gw.alicdn.com/tfs/TB1PBcecoz1gK0jSZLeXXb9kVXa-50-50.png);
      background-size: cover;
      right: vw(15);
      top: vw(15);
    }
  }
  .body{
    background: #F2F2F2;
    height: 70vh;
  }
}

.App-shop{
  top: -10px;
  z-index: 10;
  position: relative;
  .title{
    background: #fff;
    border-radius: 15px 15px 0 0;
    font-weight: bold;
    color: #333;
    height: vw(50);
    line-height: vw(50);
    text-align: left;
    padding-left: vw(14);
  }
  .body{
    background: #F2F2F2;
    min-height: vw(50);
  }
}
.fishpond-info{
  width: vw(156);
  height: vw(41);
  box-sizing: border-box;
  background: url(https://gw.alicdn.com/tfs/TB1flpJbKbviK0jSZFNXXaApXXa-622-162.png);
  background-size: cover;
  position: absolute;
  left: vw(12);
  bottom: vw(25);
  padding-left: vw(43);
  padding-top: vw(4);
  text-align: left;
  z-index: 10;
  .pondimg{
    height:vw(33);
    width:vw(33);
    background: #FFCFCF;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    left: vw(5);
    top: vw(4);
  }
  .title{
    font-size: vw(12);
    color: #000;
  }
  .desc{
    font-size: vw(10);
    transform: scale(.9);
    color: #A09991;
    text-align: left;
    font-weight: bold;
    position: relative;
    left:vw(-5);
  }
}


.qipao{
  animation: mainAnime steps(50) 1000ms infinite;
}
@keyframes mainAnime {
  to {
    background-position: 0x -4000px;
  }
} 
